.billboard {
  display: flex;
  justify-content: center;
  align-items: baseline;
  width: 100%;
  z-index: 0;
  margin-bottom: 0.5rem;
}

.billboard-banner-image-container {
  background-color: #f8f8f8;
  height: 20vw;
  width: 100%;
  max-width: 1345px;
  overflow: hidden;
}
.billboard-banner-image-container img {
  object-fit: contain;
  object-position: initial;
}

.billboard-content-box {
  display: flex;
  flex-direction: column;
  box-shadow: none;
  background: #fff;
  position: absolute;
  box-shadow: 0 2px 4px rgb(0 0 0 / 8%), 0 4px 12px rgb(0 0 0 / 8%);
  padding: 1rem;
  margin: 0;
  line-height: 1;
  max-width: 400px;
  transform: translate(-130%, 55px);
}
.billboard-content-box h2 {
  font-size: 2.2rem;
  margin: 0;
  padding: 0 0 4px 0;
  font-weight: 600;
}
.billboard-content-box p {
  margin: 0 2rem 0 0;
  padding: 0;
  line-height: 1.5;
  font-weight: lighter;
}

.headline {
  margin: 0rem 2rem;
  line-height: 1.4;
}
.headline h2 {
  font-size: 2rem;
  margin: 0.5rem 0;
  padding: 0;
  font-weight: 600;
}
.headline p {
  margin: 0.5rem 0;
  padding: 0;
}
@media only screen and (max-width: 1800px) {
  .billboard-content-box {
    transform: translate(-17vw, 55px);
  }
}
@media only screen and (max-width: 1300px) {
  .billboard-banner-image-container {
    background-color: #f8f8f8;
    min-height: 180px;
    min-width: 100%;
  }
}
@media only screen and (max-width: 1300px) {
  .billboard-content-box {
    width: 300px;
    transform: translate(-25vw, 15px);
  }
}

@media only screen and (max-width: 850px) {
  .billboard-content-box {
    position: relative;
    display: block;
    flex-direction: column;
    box-shadow: none;
    box-shadow: 0 0px 0px rgb(0 0 0 / 8%), 0 0px 0px rgb(0 0 0 / 8%);
    margin: 0;
    line-height: 1.2;
    min-width: 100%;
    max-width: none;
    height: auto;
    padding: 0rem;
    transform: translate(0, 0);
    margin-left: 2rem;
  }
  .billboard {
    flex-direction: column;
  }
}
